import { Component, ViewEncapsulation, ChangeDetectionStrategy, ChangeDetectorRef, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { Expressions, AbstractFormFieldWidget } from '../../../core';

@Component({
  selector: 'wdg-autocomplete',
  template: `<mat-form-field style.width="100%">

  <mat-label *ngIf="title">
    {{ title }}
  </mat-label>

  <input matInput name="aaa" [type]="type || 'text'" [formControl]="formControl" [placeholder]="placeholder"
    [matAutocomplete]="auto">

  <mat-error [id]="null">

  </mat-error>

  <mat-hint *ngIf="description" [id]="null">
    {{ description }}
  </mat-hint>

  <mat-autocomplete #auto="matAutocomplete">
    <mat-option *ngFor="let option of filteredOptions | async; index as optIndex" [value]="option">
      {{option}}
    </mat-option>
  </mat-autocomplete>

</mat-form-field>
`,
  styles: [``],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AutocompleteWidgetComponent extends AbstractFormFieldWidget implements OnInit {

  title: string;
  description: string;
  placeholder: string;

  enum: string[] = [];
  enumLabel: string[];
  filteredOptions: Observable<string[]>;
  constructor(cdr: ChangeDetectorRef, expr: Expressions) {
    super(cdr, expr);
  }



  dynOnBeforeBind() {
    this.map('enum', val => {
      return Array.isArray(val) ? val : [];
    });
  }

  dynOnAfterBind() {
    this.map('enum', val => (this._filter(this.formControl.value), val));
  }
  ngOnInit() {
    super.ngOnInit();

    this.filteredOptions = this.formControl.valueChanges
      .pipe(
        startWith(''),
        map(value => this._filter(value))
      );
  }

  private _filter(value: string): string[] {

    const filterValue = value && value.toLowerCase();

    return this.enum.filter(option => option.toLowerCase().includes(filterValue));
  }

}
